@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
html{font-family:"Roboto", Arial, "Helvetica Neue", Helvetica, sans-serif;color:white}.App{width:600px;display:flex;flex-direction:column;align-items:center;background:linear-gradient(36deg, #881030 0%, #6a0e7c 35%, #1eadc9 100%);border-radius:30px;padding:20px}.App__position{margin:100px auto}.App__header{display:flex;align-items:center;grid-gap:20px;gap:20px;background:rgba(156,156,156,0.3);padding:0 20px;border-radius:10px;min-width:60%}.App__button{display:flex;outline:none;width:100px;justify-content:center;align-items:center;background:none;border:1px solid rgba(205,205,205,0.5);border-radius:6px;height:30px;color:#f3f3f3;transition:all 0.4s ease-in-out;font-size:16px}.App__button:hover,.App__button:focus{background:#ffc800;color:rgba(0,0,0,0.75);border:1px solid #ffc800;cursor:pointer}.App__remove{transition:all 0.4s ease-in-out}.App__remove:hover,.App__remove:focus{background:#f03;color:black;border:1px solid #f03}.App__select{transition:all 0.4s ease-in-out}.App__select:hover,.App__select:focus{background:#2ed58f;color:black;border:1px solid #2ed58f}.App__ul{display:flex;flex-direction:column;grid-gap:6px;gap:6px}.Good{display:flex;align-items:center;background:rgba(156,156,156,0.3);border:1px solid rgba(156,156,156,0.3);border-radius:4px;justify-content:space-between;min-width:300px;padding:4px 20px}.Good--active{background-color:rgba(255,255,0,0.4)}

